/**
 * Ammo.js wrapper with TypeScript support
 */
import { AmmoInitOptions, AmmoInstance, AmmoModule } from "./types";
import { createLocateFileFunction } from "./wasm-loader";

// Re-export types
export type { AmmoInitOptions, AmmoInstance, AmmoModule };

// Define the global variables for Node.js environments
declare const global: {
  URL: any;
  [key: string]: any;
};
declare const __filename: string;

// URL constructor for Node.js environments if it doesn't exist
if (typeof URL === "undefined" && typeof global !== "undefined") {
  // @ts-ignore - Node.js URL polyfill for older versions
  global.URL = require("url").URL;
}

/**
 * Get the base URL for the current script
 */
function getBaseUrl(): string {
  // Browser environment
  if (typeof document !== "undefined" && document.currentScript) {
    const scriptUrl = (document.currentScript as HTMLScriptElement).src;
    return scriptUrl.substring(0, scriptUrl.lastIndexOf("/") + 1);
  }

  // Node.js environment
  if (typeof __filename !== "undefined") {
    return __filename.substring(0, __filename.lastIndexOf("/") + 1);
  }

  // Web Worker environment
  if (typeof self !== "undefined" && "location" in self) {
    return self.location.href.substring(
      0,
      self.location.href.lastIndexOf("/") + 1
    );
  }

  // Fallback to empty string if we can't determine the base URL
  return "/";
}

/**
 * Type for the Ammo.js module import
 */
interface AmmoWasmModule {
  default?: any;
  Ammo?: any;
}

/**
 * Dynamically import the Ammo.js WASM module
 */
async function importAmmoModule(): Promise<AmmoWasmModule> {
  try {
    // Import the module dynamically to avoid bundling issues
    // @ts-ignore - Using dynamic import
    return await import("./ammo.wasm.cjs");
  } catch (error) {
    console.error("Failed to import Ammo.js module:", error);
    throw error;
  }
}

/**
 * Initialize Ammo.js with the provided options
 */
export async function initAmmo(
  options: AmmoInitOptions = {}
): Promise<AmmoInstance> {
  // Import the Ammo module
  const ammoModule = await importAmmoModule();

  // Extract the initialization function
  const AmmoInit = ammoModule.default || ammoModule.Ammo || ammoModule;

  // Use the WASM loader utilities to create the locateFile function
  const locateFile =
    options.locateFile || createLocateFileFunction(options.wasmPath);

  // Initialize Ammo.js with the provided options
  const ammo = await AmmoInit.bind(window)({
    locateFile,
    ...options,
  });

  // Call the onReady callback if provided
  if (options.onReady) {
    options.onReady(ammo);
  }

  // Return the initialized Ammo instance
  return ammo;
}

// Export the initAmmo function for consistency
// We use named exports for better compatibility with various module systems
export const Ammo = initAmmo;

// Note: We're intentionally NOT using default export to avoid
// confusion with named exports (which causes the warning in the build)

// If we're in a browser environment, add Ammo to the window object to support global usage
if (typeof window !== "undefined") {
  // @ts-ignore - Add Ammo to window
  window.Ammo = initAmmo;
}
