
// Ammo.wasm as base64-encoded data URL
const ammoWasmBase64 = '';

// Convert base64 to binary
function base64ToBinary(base64) {
  const raw = atob(base64);
  const rawLength = raw.length;
  const array = new Uint8Array(new ArrayBuffer(rawLength));
  
  for(let i = 0; i < rawLength; i++) {
    array[i] = raw.charCodeAt(i);
  }
  
  return array.buffer;
}

// Create a Blob URL from the binary data
const binaryData = base64ToBinary(ammoWasmBase64);
const blob = new Blob([binaryData], {type: 'application/wasm'});
const wasmUrl = URL.createObjectURL(blob);

// Export the URL that can be used for instantiating WebAssembly
export default wasmUrl;

// Alternatively, export as data URL (but this might be less efficient)
export const ammoWasmDataUrl = 'data:application/wasm;base64,' + ammoWasmBase64;
