/**
 * Utility functions for loading WASM files
 */
/** @ts-ignore */
import { ammoWasmDataUrl } from "./ammo-wasm-inline.js";

// Define the global variables for Node.js environments
declare const __filename: string;
declare const __dirname: string;
declare const process: { platform: string };

/**
 * Determines if a URL is a remote URL (http/https)
 */
export function isRemoteUrl(url: string): boolean {
  return /^(https?:)?\/\//i.test(url);
}

/**
 * Converts a relative path to an absolute URL
 */
export function resolveRelativePath(
  relativePath: string,
  baseUrl = ""
): string {
  // If it's already an absolute URL, return it as is
  if (isRemoteUrl(relativePath)) {
    return relativePath;
  }

  // Remove trailing slash from baseUrl if present
  let base = baseUrl.endsWith("/") ? baseUrl.slice(0, -1) : baseUrl;

  // Ensure the relative path doesn't start with a slash
  let path = relativePath.startsWith("/")
    ? relativePath.slice(1)
    : relativePath;

  // Combine and return
  return `${base}/${path}`;
}

/**
 * Get the URL of the currently executing script (in browser environments)
 */
export function getCurrentScriptUrl(): string {
  // Check if we're in a browser environment
  if (typeof document === "undefined" || !document.currentScript) {
    return "";
  }

  const scriptUrl = (document.currentScript as HTMLScriptElement).src;
  const lastSlashIndex = scriptUrl.lastIndexOf("/");

  // Return the directory containing the script
  return lastSlashIndex >= 0
    ? scriptUrl.substring(0, lastSlashIndex + 1)
    : scriptUrl;
}

/**
 * Get the base directory URL for the current module
 */
export function getBaseUrl(): string {
  // Try browser environment first
  const scriptUrl = getCurrentScriptUrl();
  if (scriptUrl) {
    return scriptUrl;
  }

  // Try Node.js environment
  if (typeof __filename !== "undefined") {
    // Convert __filename to a URL format
    let fileUrl = "";
    if (typeof process !== "undefined" && process.platform === "win32") {
      // Windows path
      fileUrl = "file:///" + __filename.replace(/\\/g, "/");
    } else {
      // Unix path
      fileUrl = "file://" + __filename;
    }

    return fileUrl.substring(0, fileUrl.lastIndexOf("/") + 1);
  }

  // Try Web Worker environment
  if (typeof self !== "undefined" && "location" in self) {
    return self.location.href.substring(
      0,
      self.location.href.lastIndexOf("/") + 1
    );
  }

  // Fallback to current directory if we can't determine
  return "/";
}

/**
 * Create a locateFile function for Ammo.js initialization
 */
export function createLocateFileFunction(
  wasmPath?: string
): (filename: string, prefix: string) => string {
  const baseUrl = getBaseUrl();
  const defaultWasmPath = `${baseUrl}ammo.wasm.wasm`;

  // Use the provided wasmPath or default to the same directory
  const resolvedWasmPath = wasmPath || defaultWasmPath;

  return (filename: string, prefix: string): string => {
    return ammoWasmDataUrl;
  };
}

/**
 * Load a WASM file in Node.js environment
 */
export function loadWasmBinaryNode(wasmPath: string): Uint8Array | null {
  if (typeof process === "undefined" || typeof require === "undefined") {
    return null;
  }

  try {
    const fs = require("fs");
    const path = require("path");

    // Resolve the path relative to the current file if it's not absolute
    const resolvedPath = path.isAbsolute(wasmPath)
      ? wasmPath
      : path.resolve(__dirname, wasmPath);

    // Load the file
    const buffer = fs.readFileSync(resolvedPath);

    // Convert Buffer to Uint8Array
    return new Uint8Array(buffer);
  } catch (error) {
    console.error("Error loading WASM file in Node.js:", error);
    return null;
  }
}
